<template>
  <div class="px-4">
    <nuxt-link
      :to="props.to"
      class="block rounded-md px-3 py-1 text-gray-500 transition-all hover:bg-gray-200 dark:text-gray-400 hover:dark:bg-gray-800"
      :class="{
        '!cursor-not-allowed': props.disabled,
      }"
      active-class="!bg-gray-300 dark:!bg-gray-700"
      @click.capture="onClick"
    >
      <slot />
    </nuxt-link>
  </div>
</template>

<script setup lang="ts">
const props = defineProps<{
  to: string
  disabled?: boolean
}>()

function onClick(e: MouseEvent) {
  if (props.disabled) {
    e.stopPropagation()
    e.preventDefault()
  }
}
</script>
